.App {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: rgb(136, 146, 176);
}

.App-logo {
  height: 40vmin;
}

.App-link {
  color: #09d3ac;
}

p {
  font-family: 'Nunito', sans-serif!important;
  font-size: .95rem;
  line-height: 1.32rem;
}

.card1,
.card2,
.card3,
.card4,
.card5,
.card6,
.card7 {
  position: absolute;
  border-radius: 5px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  will-change: transform;
}

.card1 {
  width: 105vw;
  height: 40vh;
  background-image: url(/prototype/static/media/PBack.04d4c512.png);
  background-position: 0% 80%;
  box-shadow: inset 0px 0px 10rem black;
}

.card2 {
  width: 15vw;
  height: 30vw;
  background-image: url(/prototype/static/media/loginscreen1.5d8d1e53.png);
}

.card3 {
  width: 15vw;
  height: 30vw;
  background-image: url(/prototype/static/media/postspage.c9a6aeef.png);
}

.card4 {
  width: 15vw;
  height: 30vw;
  background-image: url(/prototype/static/media/loginscreen.c5be282a.png);
}

.p-container {
  padding-top: 2vw;
  min-width: 250px;
  min-height: 150px;
  width: 25vw;
  height: 15vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

main {
  display: block;
  width: 100%;
  margin: 0rem auto;
  padding: 0rem 10rem;
}

.text-shadow {
  color: white;
  text-shadow: 0px 0px 8px rgba(43,42,42, 1);
}

.content-section {
  max-width: 1000px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  -webkit-box-align: center;
  flex-direction: column;
  align-items: flex-start;
  margin: 0rem auto;
  padding: 0rem 0rem;
}

.hero {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero h1 {
  margin-bottom: 1.25rem;
  color: #fff;
  font-family: "Mansalva", cursive !important;
  font-size: 4rem;
  line-height: 4rem;
}

h2 {
  margin-bottom: 0rem;
  color: #fff;
  font-family: "Mansalva", cursive !important;
  font-size: 2.5rem;
  line-height: 2rem;
}

h3 {
  margin-bottom: 0rem;
  color: #fff;
  font-family: "Nunito", sans-serif !important;
  font-weight: bold;
  font-size: 1rem;
  line-height: 1rem;
}

h3 span {
  color: rgb(136, 146, 176);
  font-family: "Actor", sans-serif !important;
  font-weight: bold;
  font-size: 1.05rem;
  line-height: 1.5rem;
}

h4 {
  margin-bottom: 0rem;
  color: rgb(136, 146, 176);
